.container {
  padding: 1rem;
}

#row {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.loading-message {
  text-align: center;
  color: #7f8c8d;
  margin-top: 20px;
  font-size: 1.2rem;
}


.col {
  flex: 1 1 calc(25% - 1rem);
  /* 4 cards per row */
}

@media (max-width: 1024px) {
  .col {
    flex: 1 1 calc(33.33% - 1rem);
    /* 3 cards per row */
  }
}

@media (max-width: 768px) {
  .col {
    flex: 1 1 calc(50% - 1rem);
    /* 2 cards per row */
  }
}

@media (max-width: 480px) {
  .col {
    flex: 1 1 100%;
    /* 1 card per row */
  }
}

.container .row .col a {
  text-decoration: none;
  /* Removes the underline */
  color: inherit;
  /* Inherits color from parent element */
}

.container .row .col a:hover {
  text-decoration: none;
  /* Ensures no underline on hover */
  color: yellow;
  /* Keeps color consistent on hover */
}